	module.exports = {"data":{"markdownRemark":{"html":"<h2>CSS Display</h2>\n<p>The display property specifies the type of box used for an HTML element. It has 20 possible keyword values. The commonly used ones are:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code>    <span class=\"token selector\">.none</span>             <span class=\"token punctuation\">{</span><span class=\"token property\">display</span><span class=\"token punctuation\">:</span> none<span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.block</span>            <span class=\"token punctuation\">{</span><span class=\"token property\">display</span><span class=\"token punctuation\">:</span> block<span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.inline-block</span>     <span class=\"token punctuation\">{</span><span class=\"token property\">display</span><span class=\"token punctuation\">:</span> inline-block<span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.inline</span>           <span class=\"token punctuation\">{</span><span class=\"token property\">display</span><span class=\"token punctuation\">:</span> inline<span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.flex</span>             <span class=\"token punctuation\">{</span><span class=\"token property\">display</span><span class=\"token punctuation\">:</span> flex<span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.inline-flex</span>      <span class=\"token punctuation\">{</span><span class=\"token property\">display</span><span class=\"token punctuation\">:</span> inline-flex<span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.inline-table</span>     <span class=\"token punctuation\">{</span><span class=\"token property\">display</span><span class=\"token punctuation\">:</span> inline-table<span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.table</span>            <span class=\"token punctuation\">{</span><span class=\"token property\">display</span><span class=\"token punctuation\">:</span> table<span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.inherit</span>          <span class=\"token punctuation\">{</span><span class=\"token property\">display</span><span class=\"token punctuation\">:</span> inherit<span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.initial</span>          <span class=\"token punctuation\">{</span><span class=\"token property\">display</span><span class=\"token punctuation\">:</span> initial<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>More Information:</h3>\n<p>Docs and full list of keyword values: <a href='https://developer.mozilla.org/en-US/docs/Web/CSS/display' target='_blank' rel='nofollow'>MDN</a></p>\n<p>Browser Support: <a href='http://caniuse.com/#search=display' target='_blank' rel='nofollow'>caniuse</a></p>\n<p>A Complete Guide to Flexbox: <a href='https://css-tricks.com/snippets/css/a-guide-to-flexbox/' target='_blank' rel='nofollow'>CSS-Tricks</a></p>","fields":{"slug":"/css/css-display/"},"frontmatter":{"title":"CSS Display"}}},"pathContext":{"slug":"/css/css-display/"}}

